/* index.css */
/* Estilos globales pueden ir aquí */


body {
  margin: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #555555; /* Opcional: para un fondo más agradable */
}
/*
.app-container {
  width: 650px; 
  height: 400px;
}
*/

.app-container {
  width: 100%; /* Cambiado a un porcentaje para que sea más flexible */
  max-width: 650px; /* Tamaño máximo para pantallas grandes */
  height: 400px;
}

@media (max-width: 768px) { /* Para tabletas y dispositivos móviles en orientación horizontal */
  .app-container {
    height: auto; /* Permite que el contenedor crezca según el contenido */
    max-width: 90%; /* Limita el ancho máximo en dispositivos más pequeños */
  }
}

@media (max-width: 480px) { /* Para dispositivos móviles en orientación vertical */
  .app-container {
    max-width: 100%; /* Utiliza todo el ancho disponible */
  }
}



