.app-container {
  width: 400px;
  height: 400px;
}

.calculator {
  display: grid;
  grid-template-columns: repeat(8, 50px);
}

.banner {
  width: 398px; /* Ocupa todo el ancho disponible */
  font-size: 12px;
  text-align: right;
  border: 1px solid greenyellow;
  padding-right: 0px;
  padding-bottom: 5px;
  background-color: black;
  color: greenyellow;
}

/*
.banner h3 {
  margin: 0; 
}
*/

.banner p {
  padding: 0;
	margin: 0;
	/* font-family: century gothic; */
	font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;  
	text-align: left;
	box-sizing: border-box;
}



.display {
  grid-column-start: 1;
  grid-column-end: 9;
  height: 80px;
  font-size: 30px;
  text-align: right;
  border: 1px solid black;
  padding-right: 10px;
  padding-top: 5px;
  background-color: #0000aa;
  color: white;
}

button {
  height: 50px;
  color: black;
  background-color: white;
  border: 1px solid lightgray;
}

.clear-button {
  grid-column-start: 1;
  grid-column-end: 9;
  height: 40px;
  font-size: 20px;
  text-align: center;
  border: none;
  background-color: #f07a7a;
  color: white;
}

button.operator {
  background-color: #5555ff;
  color: white;
}

button.operator:hover {
  color: white;
  /* background-color: #00ce00; */
  background-color: #4343ff;
  transition: background-color 0.1s linear;
}

button.igual:hover {
  color: rgb(0, 0, 0);
  background-color: #7FFFD4;
  transition: background-color 0.1s linear;
}

.clear-button:hover {
  color: rgb(0, 0, 0);
  background-color: #FF4500;
  transition: background-color 0.1s linear;
}